@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sent {
  display:grid;
  grid-template-columns: 1fr 9fr;
}

section {
  width: 50%;
  margin: auto;
  background-color: white;
  padding: 5px 20px;
  border-radius: 15px;
}

form {
  width: 50%;
  margin: auto;
}

.left img {
  border-radius: 50%;
  width: 50%;
  margin-bottom: 10px;
  margin-top: auto;
}

.left {
  display: flex;
  flex-direction: column-reverse;
}

.userName {
  font-size: 0.75vw;
  margin-bottom: 2px;
}

.right {
  display: flex;
  flex-direction: column;
}

.textContent {
  font-size: 1.5vw;
  padding: 0.5vw 0.7vw;
  border-radius: 20px;
  margin: 5px auto 0px 5px;
  background-color: rgb(228, 227, 233);
}

.message {
  margin: 10px 0px;
}

body {
  background-color: black;
}

header {
  text-align: center;
  padding-bottom: 5px;
  margin-bottom: 10px;
}

h1 {
  color: white;
  margin: 0px;
  padding-top: 15px;
  padding-bottom: 3px;
}

form {
  margin: 0px;
  width: 100%;
}

input {
  box-sizing: border-box;
  width: 100%;
  border-radius: 15px;
  padding: 5px;
  margin-bottom: 5px;
}

* {
  font-family: 'Work Sans', sans-serif;
}
